import uniIcon from "@/components/uni-icon/uni-icon.vue"
import uniBadge from "@/components/uni-badge/uni-badge.vue"
export default {
	components: {
		uniIcon,
		uniBadge,
	},
	data() {
		return {
			tabBar: [{
					"title": "个人信息"
				},
				{
					"title": "租赁信息"
				},
			],
			currentTab: 0,
			type:1,
		}
	},
	methods: {
		onLoad: function (option) { //option为object类型，会序列化上个页面传递的参数
			this.type=option.type;
		},
		clickTab(e) {
			this.currentTab = e;
		},
		changeTab(e) {
			this.currentTab = e.mp.detail.current;
		},
		update_type(type){
			if(type==2){
				uni.navigateTo({
					url: `withdrawal?type=${type}`,
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
		},
		// 跳转信息
		look_news(){
			uni.navigateTo({
				url: `news`,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		// 跳转合同
		contract(){
			uni.navigateTo({
				url: `../contract`,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		// 跳转到租费
		rates(){
			uni.navigateTo({
				url: `../rates`,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		// 退租续租
		terminate(key){
			uni.navigateTo({
			  url:`../request?key=${key}`,
			  success: res => {},
			  fail: () => {},
			  complete: () => {}
			});
		},
		call(phoneNumber){
			uni.makePhoneCall({
				phoneNumber: phoneNumber //仅为示例
			});
		}
	}
}
